@charset "utf-8";

@import url('http://fonts.googleapis.com/earlyaccess/nanumgothic.css');

*{
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
  font-family: 'nanumgothic';
}
body{
  background-color: rgb(253, 243, 230);
}

ul, li{
  list-style: none;
}

ul{
  display: flex;
  flex-wrap: wrap;
}

h1{
  text-align: center;
  padding: 10px;
}

/* TodoListTemplate - 입력폼, 출력폼 포함하는 부모컴포넌트 */
.TodoListTemplate{
  width: 95%;
  margin: 3rem auto 0 auto;
  background-color: #0038C8;
  box-shadow: 0 3px 6px #ccc;
  border-radius: 10px;
}
.title{
  padding: 2rem;
  text-align: center;
  color: white;
  font-size: 2rem;
  font-weight: bold;
}
.form_wrapper{
  background-color: #E6ECFA;
  padding: 1rem;
}
.todos_wrapper{
  background-color: #E6ECFA;
  min-height: 30rem;
}

.form{
  text-align: center;
  margin: 20px 0;
  display: flex;
}
input{
  flex:content;
  /* 부모를 flex로 한 상태로 content를 하면 꽉 채워진다 */
  height: 40px;
  border-radius: 5px;
  padding: 5px 10px;
  border: none;
  margin: 0 10px;
  font-size: 1.4rem;
  outline: none;
}
.btn{
  background-color: #0038C8;
  border-radius: 5px;
  color: white;
  font-weight: 500;
  border: none;
  height: 40px;
  width: 60px;
}
.create_btn{
  background-color: #0038C8;
  font-size: 1.2rem;
}
.create_btn:hover{
  background-color: blue;
}
.delete_btn{
  background-color: #A61313;
  font-size: 2rem;
  text-align: center;
  line-height: 40px;
}

.todo_item{
  display: flex;
  align-items: center;
  cursor: pointer;
  width: 95%;
  margin: 0 auto;
}
.todo_item:hover{
  background-color: #d9d9d9;
  border-radius: 5px;
}
.todo_item:hover .remove{
  opacity: 1;
}
.remove{
  opacity: 0;
}
.todo_text{
  text-align: center;
  flex: 1;
}
.check_mark{
  color: #A61313;
  font-weight: bold;
}

.checked{
  text-decoration: line-through;
  color: #A61313;
}